export default {
	data() {
		return {
			form: {
				name: "",
				sex: "",
			},
			list: []
		}
	},
	methods: {
		// 注册账号
		fnSubmit() {
			uniCloud.callFunction({
				name: "addUser",
				data: this.form,
				success: (e) => {
					this.fnGetUser();
				}
			})
		},
		// 获取账号
		fnGetUser() {
			uniCloud.callFunction({
				name: "getUser",
				success: (e) => {
					console.log(e.result);
					this.list = e.result.data;
				}
			})
		},
		// 删除某条数据
		fnDelete(_id) {
			uniCloud.callFunction({
				name: "deleteUser",
				data: {
					"id": _id,
				},
				success: (e) => {
					this.fnGetUser();
				}
			})
		},
		// 更新数据
		fnUpdata(_id) {
			uniCloud.callFunction({
				name: "updataUser",
				data: {
					"id": _id,
				},
				success: (e) => {
					console.log(e)
					this.fnGetUser();
				}
			})
		},
		// 指定条件获取数据
		fnGetWhere() {
			// 按条件获取
			uni.callFunction({
				name: "getUserWhere",
				success: (e) => {
					console.log(e.result);
					this.list = e.result.data;
				}
			})
		}
	},
	created() {
		// this.fnGetUser();
		this.fnGetWhere();
	}
}
